<template>
    <!-- container -->
    <div class="container" :class="isContorlPopupOpen || isAllAlarmOpen? 'cctv':'event setting'">
        <div class="inner">
            <h2 class="page_title">돌발이벤트관리</h2>
            <div class="content_box">
                <!-- input_group -->
                <div class="input_group">
                    <div class="input_box mr30">
                        <select class="select" v-model="searchScanStatus"  @change="fnSearch">
                            <option value="">전체</option>
                            <template v-for="(sts,sIdx) in 2">
                                <option :value="sts" :key="sIdx">{{sts}}차검지</option>
                            </template>
                            
                        </select>
                    </div>
                    <div class="input_box mr30">
                        <select class="select" v-model="searchScanTyCode" @change="fnSearch">
                            <option value="">전체</option>
                            <template v-for="(ty,tyIdx) in $store.state.event.scanTyCodeList">
                                <option :value="ty.DETAIL_CODE_ID" :key="tyIdx">{{ty.DETAIL_CODE_NM}}</option>
                            </template>
                        </select>
                    </div>
                    <div class="input_box mr30">
                        <select class="select" v-model="searchScanDrcCode" @change="fnSearch">
                            <option value="">전체</option>
                            <template v-for="(ty,tyIdx) in $store.state.event.scanDrcCodeList">
                                <option :value="ty.DETAIL_CODE_ID" :key="tyIdx">{{ty.DETAIL_CODE_NM}}</option>
                            </template>
                        </select>
                    </div>
                    <!------ 버튼 클릭시 DatePicker 생성 => datePicker 안에 input박스 출력 ----->
                </div>                    
                <!-- // input_group -->
                <!-- table -->
                <!-- 헤더 고정 테이블인 경우 아래와 같이 사용해주세요. -->
                <div class="fixed_table_box txt_r">
                    <div class="table_hd">
                        <table>
                             <colgroup>
                                <col class="w6p">
                                <col class="w10p">
                                <col class="w20p">
                                <col class="w16p">
                                <col class="w8p">
                                <col class="w8p">
                                <col class="w8p">
                                <col class="w8p">
                                <col class="w8p">
                                <col class="w8p">                                
                            </colgroup>
                            <tbody>
                                <tr class="table_hd_category">
                                    <td colspan="6"><span>1차 검지</span></td>
                                    <td colspan="2"><span>2차 검지</span></td>
                                    <td colspan="2"><span>결과비교</span></td>
                                </tr>
                                <tr>
                                    <td>
                                        <span>상황</span>
                                        <div class="input_box sort_box">
                                            <button class="btn_sort" v-if="currOrder=='SCAN_GRAD' && currOrderAlign =='DESC'"><img src="/static/EXcode/static/images/ic_arrow_up_wh.png" alt="" @click="fnListOrder('SCAN_GRAD','ASC')"></button>
                                            <button class="btn_sort" v-if="(currOrder=='SCAN_GRAD' && currOrderAlign =='ASC') || currOrder != 'SCAN_GRAD'"><img src="/static/EXcode/static/images/ic_arrow_down_wh.png" alt="" @click="fnListOrder('SCAN_GRAD','DESC')"></button>
                                        </div>
                                    </td>
                                    <td>
                                        <span>CCTV ID</span>
                                        <div class="input_box sort_box">
                                            <button class="btn_sort" v-if="currOrder == 'CCTV_ID' && currOrderAlign == 'DESC'"><img src="/static/EXcode/static/images/ic_arrow_up_wh.png" alt="" @click="fnListOrder('CCTV_ID','ASC')"></button>
                                            <button class="btn_sort" v-if="(currOrder == 'CCTV_ID' && currOrderAlign == 'ASC') || currOrder !='CCTV_ID'"><img src="/static/EXcode/static/images/ic_arrow_down_wh.png" alt="" @click="fnListOrder('CCTV_ID','DESC')"></button>
                                        </div>
                                    </td>
                                    <td>
                                        <span>설치위치</span>
                                        <div class="input_box sort_box">
                                            <button class="btn_sort" v-if="currOrder == 'INSTL_LC_ABRV' && currOrderAlign == 'DESC'"><img src="/static/EXcode/static/images/ic_arrow_up_wh.png" alt="" @click="fnListOrder('INSTL_LC_ABRV','ASC')"></button>
                                            <button class="btn_sort" v-if="(currOrder == 'INSTL_LC_ABRV' && currOrderAlign == 'ASC') || currOrder !='INSTL_LC_ABRV'"><img src="/static/EXcode/static/images/ic_arrow_down_wh.png" alt="" @click="fnListOrder('INSTL_LC_ABRV','DESC')"></button>
                                        </div>
                                    </td>
                                    <td>
                                        <span>검지일시</span>
                                        <div class="input_box sort_box">
                                            <button class="btn_sort" v-if="currOrder == 'SCAN_DT' && currOrderAlign == 'DESC'"><img src="/static/EXcode/static/images/ic_arrow_up_wh.png" alt="" @click="fnListOrder('SCAN_DT','ASC')"></button>
                                            <button class="btn_sort" v-if="(currOrder == 'SCAN_DT' && currOrderAlign == 'ASC') || currOrder !='SCAN_DT'"><img src="/static/EXcode/static/images/ic_arrow_down_wh.png" alt="" @click="fnListOrder('SCAN_DT','DESC')"></button>
                                        </div>
                                    </td>
                                    <td>
                                        <span>검지유형</span>
                                        <div class="input_box sort_box">
                                            <button class="btn_sort" v-if="currOrder == 'SCAN_TY_CODE' && currOrderAlign == 'DESC'"><img src="/static/EXcode/static/images/ic_arrow_up_wh.png" alt="" @click="fnListOrder('SCAN_TY_CODE','ASC')"></button>
                                            <button class="btn_sort" v-if="(currOrder == 'SCAN_TY_CODE' && currOrderAlign == 'ASC') || currOrder !='SCAN_TY_CODE'"><img src="/static/EXcode/static/images/ic_arrow_down_wh.png" alt="" @click="fnListOrder('SCAN_TY_CODE','DESC')"></button>
                                        </div>
                                    </td>
                                    <td>
                                        <span>검지방향</span>
                                        <div class="input_box sort_box">
                                            <button class="btn_sort" v-if="currOrder == 'SCAN_DRC_CODE' && currOrderAlign == 'DESC'"><img src="/static/EXcode/static/images/ic_arrow_up_wh.png" alt="" @click="fnListOrder('SCAN_DRC_CODE','ASC')"></button>
                                            <button class="btn_sort" v-if="(currOrder == 'SCAN_DRC_CODE' && currOrderAlign == 'ASC') || currOrder !='SCAN_DRC_CODE'"><img src="/static/EXcode/static/images/ic_arrow_down_wh.png" alt="" @click="fnListOrder('SCAN_DRC_CODE','DESC')"></button>
                                        </div>
                                    </td>
                                    <td>
                                        <span>검지유형</span>
                                        <div class="input_box sort_box">
                                            <button class="btn_sort" v-if="currOrder == 'SCD_SCAN_TY_CODE' && currOrderAlign == 'DESC'"><img src="/static/EXcode/static/images/ic_arrow_up_wh.png" alt="" @click="fnListOrder('SCD_SCAN_TY_CODE','ASC')"></button>
                                            <button class="btn_sort" v-if="(currOrder == 'SCD_SCAN_TY_CODE' && currOrderAlign == 'ASC') || currOrder !='SCD_SCAN_TY_CODE'"><img src="/static/EXcode/static/images/ic_arrow_down_wh.png" alt=""  @click="fnListOrder('SCD_SCAN_TY_CODE','DESC')"></button>
                                        </div>
                                    </td>
                                    <td>
                                        <span>검지방향</span>
                                        <div class="input_box sort_box">
                                            <button class="btn_sort" v-if="currOrder == 'SCD_SCAN_DRC_CODE' && currOrderAlign == 'DESC'"><img src="/static/EXcode/static/images/ic_arrow_up_wh.png" alt="" @click="fnListOrder('SCD_SCAN_DRC_CODE','ASC')"></button>
                                            <button class="btn_sort" v-if="(currOrder == 'SCD_SCAN_DRC_CODE' && currOrderAlign == 'ASC') || currOrder !='SCD_SCAN_DRC_CODE'"><img src="/static/EXcode/static/images/ic_arrow_down_wh.png" alt="" @click="fnListOrder('SCD_SCAN_DRC_CODE','DESC')"></button>
                                        </div>
                                    </td>                                        
                                    <td>
                                        <span>검지비교</span>
                                        <div class="input_box sort_box">
                                            <button class="btn_sort" v-if="currOrder == 'SCAN_TY_DIV' && currOrderAlign == 'DESC'"><img src="/static/EXcode/static/images/ic_arrow_up_wh.png" alt="" @click="fnListOrder('SCAN_TY_DIV','ASC')"></button>
                                            <button class="btn_sort" v-if="(currOrder == 'SCAN_TY_DIV' && currOrderAlign == 'ASC') || currOrder !='SCAN_TY_DIV'"><img src="/static/EXcode/static/images/ic_arrow_down_wh.png" alt="" @click="fnListOrder('SCAN_TY_DIV','DESC')"></button>
                                        </div>
                                    </td>
                                    <td>
                                        <span>이벤트등록</span>
                                    </td>
                                </tr>
                            </tbody>
                        </table>                         
                    </div>
                    <div class="table_bd scroll_box">
                        <table>
                            <colgroup>
                                <col class="w6p">
                                <col class="w10p">
                                <col class="w20p">
                                <col class="w16p">
                                <col class="w8p">
                                <col class="w8p">
                                <col class="w8p">
                                <col class="w8p">
                                <col class="w8p">
                                <col class="w8p">
                            </colgroup>
                            <tbody v-if="isNull(eventList)==false && eventList.length > 0">
                                <template
                                    v-for="(data,dIdx) in eventList"
                                >
                                    <tr
                                        :key="dIdx"
                                    >
                                        <td>
                                            <span>{{data.SCAN_GRAD}}급</span>
                                        </td>
                                        <td>
                                            <span>{{data.CCTV_ID}}</span>
                                        </td>
                                        <td>
                                            <span>{{data.INSTL_LC_ABRV}}</span>
                                        </td>
                                        <td>
                                            <span>{{data.SCAN_DT}}</span>
                                        </td>
                                        <td>
                                            <span>{{data.SCAN_TY_CODE_NM}}</span>
                                        </td>
                                        <td>
                                            <span>{{data.SCAN_DRC_CODE_NM}}</span>
                                        </td>
                                        <td>
                                            <span>{{data.SCD_SCAN_TY_CODE_NM}}</span>
                                        </td>
                                        <td>
                                            <span>{{data.SCD_SCAN_DRC_CODE_NM}}</span>
                                        </td>
                                        <td>                                            
                                            <span v-if="data.SCAN_TY_DIV == 'Y'">일치</span>
                                            <span v-if="data.SCAN_TY_DIV == 'N'" class="discord">불일치</span>
                                        </td>
                                        <td>
                                            <span>
                                                <div class="input_box">
                                                    <button 
                                                        class="dis_ib btn btn_md btn_secondary"
                                                        @click="evtPopOpen(data)"
                                                    >
                                                        관리
                                                    </button>
                                                </div>
                                            </span>
                                        </td>
                                    </tr>
                                </template>
                            </tbody>
                            <tbody v-else>
                                <tr>
                                    <td colspan="10">
                                        <span>등록된 이벤트 내역이 없습니다.</span>
                                    </td>                                    
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
                <!-- table -->
            </div>
        </div>
        <!-- 이벤트 등록 팝업 -->
        <EventPopup
            v-if="eventPopData.open"
            @close="eventClosePop"
            :cctvID="eventPopData.cctvID"            
            :scanDT="eventPopData.scanDT"            
            :popCallType="eventPopData.popCallType"
        />
        <!-- 일괄 알람설정 -->
        <PopupAlarm 
            :isOpen="isAllAlarmOpen"
            @onClose="onAllAlarmPopupClose"
            :items="allCctvList"
        />
        <!-- CCTV 정보 팝업 -->
        <PopupCctv
            :isOpen="isContorlPopupOpen"
            :cctvId="selectedCctvId"
            @onClose="onManagerPopupClose"
        />
    </div>
    <!-- container -->    
</template>

<script>
import moment from "moment";
import { mapState, mapMutations, mapActions } from 'vuex'
import EventPopup from "@/components/event/EventPopup"
import PopupCctv from '@/components/cctv/PopupCctv'
import PopupAlarm from '@/components/cctv/PopupAlarm'
import dayjs from 'dayjs'

export default {
    data(){
        return {
            searchScanStatus : ""       // 검색조건 - 검지상태
        ,   searchScanTyCode : ""       // 검색조건 - 검지유형
        ,   searchScanDrcCode : ""      // 검색조건 - 검지방향
        ,   intdCode : this.$store.state.auth.insttId
        ,   eventList : null
        ,   currOrder : "SCAN_DT"
        ,   currOrderAlign : "ASC"
        ,   eventPopData : {
                open : false
            ,   cctvID : ""
            ,   scanDT : ""
            ,   popCallType : "EVT"
            }
        ,   isContorlPopupOpen : false
        ,   selectedCctvId:null
        ,   commCctvList: [] //공통코드 (cctv_id)
        ,   allCctvList: []     // CCTV
        ,   isAllAlarmOpen : false
        };
    }
,   components : {
        EventPopup,
        PopupCctv,
        PopupAlarm
    }
,   mounted()
    {   
        $(".fixed_table_box .table_bd").mCustomScrollbar({
            theme : "minimal-dark"
        });
        this.fnSearch(); 
    }
,   methods :{
        ...mapActions('event', ['getEventList']),
        ...mapActions('cctv', ['getCctvList'])
    ,   onMangerPopup(cctv_id) {
            this.isContorlPopupOpen=true
            this.selectedCctvId = cctv_id
            this.eventPopData.open = false                        
            this.eventPopData.popCallType = "CCTV"
        }    
        // 일괄알람팝업
    ,   onAllAlarmPopupOpen(cctvId) {
            var storeCctvList = this.$store.state.cctv.cctvList;            
            if(storeCctvList.length == 0)
            {                
                this.getCctvList({}).then(res => {
                    // 공통코드cctv_id
                    const dataList =res;
                    this.allCctvList =[...dataList.filter(item => {return item.CCTV_ID == cctvId})]
                    $(".scroll_box").mCustomScrollbar({
                        theme : "minimal-dark"
                    });
                    this.isAllAlarmOpen = true
                })
            }
            else
            {
                this.allCctvList =[...storeCctvList.filter(item => {return item.CCTV_ID == cctvId})]
                $(".scroll_box").mCustomScrollbar({
                    theme : "minimal-dark"
                });
                this.isAllAlarmOpen = true
            }
            
        }
    ,   onAllAlarmPopupClose(){
            this.isAllAlarmOpen = false
        }
    ,   onManagerPopupClose(v) {
            this.isContorlPopupOpen=false
        }      
    ,   isNull(str)
        {
            if(str == undefined || str == "" || str == null)
            {
                return true;
            }   
            else
            {
                return false;
            }
        }
    ,   dateFormat(str){
        return moment(str).format("YYYY-MM-DD HH:MM:SS")
    }
    ,   evtPopOpen(eventData)
        {
            this.eventPopData.popCallType = "EVT"
            this.eventPopOpen(eventData);
        }
        //팝업 열기
    ,   eventPopOpen(eventData)
        {       
            this.eventPopData.cctvID = eventData.CCTV_ID
            this.eventPopData.scanDT = eventData.SCAN_DT
            this.eventPopData.open=true                        
        }
        // 팝업 닫기
    ,   eventClosePop()
        {
            this.eventPopData.open=false
            this.fnSearch();
        }
    ,   async fnSearch()
        {               
            var params = {
                intdCode :  this.intdCode
            ,   searchStatus : (this.isNull(this.searchScanStatus))? "all" : this.searchScanStatus
            ,   searchScanTyCode : (this.isNull(this.searchScanTyCode))? "all" : this.searchScanTyCode
            ,   searchScanDrcCode : (this.isNull(this.searchScanDrcCode))? "all" : this.searchScanDrcCode
            ,   searchType : this.isNull(this.$store.state.event.scanDrcCodeList)? "L" : "R"
            ,   currOrder : this.currOrder
            ,   currOrderAlign : this.currOrderAlign
            }
            const result = await this.getEventList(params);
            const data = result.data;
            this.eventList = []
            this.eventList = data.eventList 
        }
    ,   fnListOrder(orderType, align){
            this.currOrder = orderType
            this.currOrderAlign = align;            
            this.fnSearch();
        }
    }
}
</script>


